import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";

export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: [],
					payState: null,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: [],
					payState: 0,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 2,
					text: '待发货',
					loadingType: 'more',
					orderList: [],
					payState: 20,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 3,
					text: '待收货',
					loadingType: 'more',
					orderList: [],
					payState: 40,
					pageSize: 10,
					pageIndex: 1
				},
				{
					state: 4,
					text: '待评价',
					loadingType: 'more',
					orderList: [],
					payState: 60,
					pageSize: 10,
					pageIndex: 1
				}
			],
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = options.state;
		this.loadData()
	},
	methods: {
		stopPrevent(){},
		navTo(url){
			uni.navigateTo({
				url
			})  
		},
		//获取订单列表
		async loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			if(source === 'tabChange'){ //切换重新刷新
				navItem.pageIndex = 1;
				navItem.orderList = [];
				navItem.loadingType = 'more';
			}
			
			if(navItem.loadingType == 'noMore'){
				return;
			}
			
			navItem.loadingType = 'loading';
			
			let values = {pageIndex: navItem.pageIndex,pageSize: navItem.pageSize};
			if(navItem.payState != null){
				values['orderStatus'] = navItem.payState;
			}
			let result = await this.$request.ModelOrder.listOrder(values);
			let orderList = result.records;
			orderList.forEach(item=>{
				item = Object.assign(item, this.orderStateExp(item.orderStatus));
				navItem.orderList.push(item);
			})
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);
			
			//判断是否还有数据， 有改为 more， 没有改为noMore 
			if(orderList.length < navItem.pageSize){
				navItem.loadingType = 'noMore';
			}else{
				navItem.loadingType = 'more'; 
			}
			
			navItem.pageIndex = navItem.pageIndex + 1;
			this.navList[index] = navItem;
			console.log('this.navList[index]',this.navList[index]);
		}, 

		//swiper 切换
		changeTab(e){
			if(this.tabCurrentIndex == e.target.current){
				return;
			}
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			if(this.tabCurrentIndex == index){
				return;
			}
			this.tabCurrentIndex = index;
			this.loadData('tabChange');
		},
		
		async deleteOrder(index){ //删除订单
			uni.showLoading({
				title: '请稍后'
			})
			let orderItem = this.navList[this.tabCurrentIndex].orderList[index];
			
			// await this.$request.ModelOrder
			
			this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
			console.log('delete order',orderItem);
			uni.hideLoading();
		},
		cancelOrder(item){//取消订单
			var that = this;
			uni.showModal({
			    title: '确定取消订单?',
			    success: function (res) {
			        if (res.confirm) {
						that.$request.ModelOrder.cancelOrder(item.orderNum).then(result => {
							that.loadData('tabChange');
						});
			        }
			    }
			});
		},
		payOrder(item){ //支付订单
			uni.navigateTo({
				url: '/pages/money/pay?orderNum='+item.orderNum
			})
		},
		logistOrder(item){ //查看物流
			let orderDetailId = item.orderDetails[0].orderDetailId;
			uni.navigateTo({
				url: '/pages/order/logistics?orderDetailId='+orderDetailId
			})
		},
		gotoComments(item){ //查看物流
			let orderDetailId = item.orderDetails[0].orderDetailId;
			uni.navigateTo({
				url: '/pages/order/comment?orderDetailId='+orderDetailId
			})
		},
		sureOrder(item){ //确认收货
			var that = this;
			let orderDetailId = item.orderDetails[0].orderDetailId;
			uni.showModal({
			    title: '确定已经收到商品?',
			    success: function (res) {
			        if (res.confirm) {
						that.$request.ModelOrder.confirmOrder(orderDetailId).then(result => {
							that.loadData('tabChange');
						});
			        }
			    }
			});
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case -10:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
				case 0:
					stateTip = '待付款';break;
				case 20:
					stateTip = '待发货'; break;
				case 40:
					stateTip = '待收货'; break;
				case 60:
					stateTip = '待评价';break;
				case 80:
					stateTip = '可售后';break;
				//更多自定义
			}
			return {stateTip, stateTipColor};
		},
		gotoGoodsDetail(item){
			if(item.orderStatus == -10){
				return;
			}
			if(item.orderStatus < 40){
				this.navTo('/pages/order/orderDetail?id='+item.orderNum + '&orderStatus=' + item.orderStatus)
			}else{
				let id = item.orderDetails[0].orderDetailId;
				this.navTo('/pages/order/orderDetail?id=' + id + '&orderStatus=' + item.orderStatus)
			}
		}
	},
}
